<template>
  <div class="executionInformation">
    <div class="header">
      <el-input
        class="input"
        size="small"
        v-model.trim="queryForm.activityKeyword"
        placeholder="请输入营销活动内容关键词"
      ></el-input>
      <el-button size="small" type="primary" @click="getTableData">查询</el-button>
    </div>
    <div class="content">
      <!-- <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span class="text">客户关注热点趋势分析</span>
          </div>
      </div>-->
      <el-table
        class="content-table"
        border
        :data="tableData"
        :height="500"
        :row-class-name="tableRowClassName"
      >
        <el-table-column label="序号" align="center" type="index" width="70"></el-table-column>
        <el-table-column prop="activityName" label="营销活动名称"></el-table-column>
        <el-table-column align="center" label="计划参与活动的客户清单">
          <template slot-scope="scope">
            <span class="text" type="text" @click="clickCheck(scope.row)">查看</span>
          </template>
        </el-table-column>
        <el-table-column prop="executePlacePlan" label="计划执行地点"></el-table-column>
        <el-table-column prop="executeTimePlan" label="计划执行时间"></el-table-column>
        <el-table-column prop="updateTime" label="更新时间"></el-table-column>
        <el-table-column align="center" label="参与活动客户清单录入">
          <template slot-scope="scope">
            <span class="text" type="text" @click="clickEntry(scope.row)">录入已执行营销活动客户清单</span>
          </template>
        </el-table-column>
        <el-table-column align="center" width="200" label="操作">
          <template slot-scope="scope">
            <!-- <span class="text" type="text">提交申请</span> -->
            <span class="text" type="text" @click="clickClose(scope.row)">关闭任务</span>
            <!-- <el-checkbox v-model="scope.row.bindTagB" @change="bindTagBChange(scope.row)">是否标记</el-checkbox> -->
          </template>
        </el-table-column>
      </el-table>
      <div class="content-page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="this.queryForm.current"
          :page-sizes="[10,20,30,40]"
          :page-size="this.queryForm.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="this.queryForm.total"
        ></el-pagination>
      </div>
    </div>
    <el-dialog
      class="check-dialog"
      title="推荐清单查看"
      :visible.sync="checkDialogVisible"
      width="80%"
      :before-close="checkDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '推荐清单查看' }}</span>
      </span>
      <div class="query-group">
        <div class="search">
          <el-select
            class="select"
            size="small"
            v-model="checkDialogForm.tagId"
            clearable
            placeholder="请选择标签"
          >
            <el-option
              v-for="item in labelOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-input
            class="input"
            size="small"
            v-model.trim="checkDialogForm.yhbh"
            placeholder="请输入用户编号"
          ></el-input>
          <el-button size="small" type="primary" @click="getCheckDialogData">查询</el-button>
        </div>
      </div>
      <el-table
        class="dialog-table"
        border
        :data="checkDialogData"
        :height="400"
        :row-class-name="tableRowClassName"
      >
        <el-table-column prop="yhbh" label="用户编号"></el-table-column>
        <el-table-column prop="yhmc" label="用电客户名称"></el-table-column>
        <el-table-column prop="yddz" label="用户地址"></el-table-column>
        <!-- <el-table-column prop="sstq" label="所属台区"></el-table-column> -->
        <!-- <el-table-column prop="tjsx" label="推荐排序"></el-table-column> -->
      </el-table>
      <div class="dialog-pagination">
        <el-pagination
          @size-change="checkDialogSizeChange"
          @current-change="checkDialogCurrentChange"
          :current-page="this.checkDialogForm.current"
          :page-sizes="[10,20,30,40]"
          :page-size="this.checkDialogForm.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="this.checkDialogForm.total"
        ></el-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="checkDialogClose">关闭</el-button>
      </span>
    </el-dialog>
    <el-dialog
      class="entry-dialog"
      title="参与活动客户标记"
      :visible.sync="entryDialogVisible"
      width="80%"
      :before-close="entryDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '参与活动客户标记' }}</span>
      </span>
      <div class="query-group">
        <div class="search">
          <el-input
            class="input"
            size="small"
            v-model.trim="entryDialogForm.yhbh"
            placeholder="请输入用户编号"
          ></el-input>
          <el-button size="small" type="primary" @click="getEntryDialogData">查询</el-button>
          <el-button size="small" type="primary" @click="downFile">下载模板</el-button>
          <el-upload
            class="upload"
            :action="actionUrl"
            :data="uploadParams"
            :on-success="handleSuccess"
            multiple
            accept=".xls, .xlsx"
            :before-upload="beforeUpload"
            :show-file-list="false"
          >
            <el-button size="small" type="primary">上传清单</el-button>
          </el-upload>
        </div>
      </div>
      <el-table
        class="dialog-table"
        border
        :data="entryDialogData"
        :height="400"
        :row-class-name="tableRowClassName"
      >
        <el-table-column prop="yhbh" label="用户编号"></el-table-column>
        <el-table-column prop="yhmc" label="用电客户名称"></el-table-column>
        <el-table-column prop="yddz" label="用户地址"></el-table-column>
<!--        <el-table-column prop="sstq" label="所属台区"></el-table-column>-->
        <el-table-column prop="algorithm" label="是否算法推荐用户">
          <template slot-scope="scope">
            {{scope.row.algorithm==="1"?'是':'否'}}
          </template>
        </el-table-column>
        <el-table-column prop="czsj" align="center" label="是否参与活动">
          <template slot-scope="scope">
            <!-- <el-link type="danger">删除{{scope.name}}</el-link> -->
            <!-- <el-checkbox v-model="scope.row.bindTagB" @change="bindTagBChange(scope.row)">是否标记</el-checkbox> -->
            <el-checkbox  v-model="scope.row.flag" :disabled="scope.row.flag" @change="activeSelect(scope.row)"></el-checkbox>
          </template>
        </el-table-column>
        <el-table-column prop="operateTime" label="操作时间"></el-table-column>
      </el-table>
      <div class="dialog-pagination">
        <el-pagination
          @size-change="entryDialogSizeChange"
          @current-change="entryDialogCurrentChange"
          :current-page="entryDialogForm.current"
          :page-sizes="[5,20,30,40]"
          :page-size="entryDialogForm.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="entryDialogForm.total"
        ></el-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="entryDialogClose">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { YDKH_TAG_OBJ } from '@/config'
import Interface from '@/base/mixin/precision-marketing';
import { CP_PATH } from '@/config'
import Qs from 'qs'
export default {
  name: "executionInformation",
  components: {
  },
  mixins: [Interface],
  props: {},
  data() {
    return {
      queryForm: {
        status: "3",
        activityKeyword: "",
        current: 1,
        size: 10,
        total: 0
      },
      // 表格
      activityId: "",
      tableData: [],
      // 查看框
      checkDialogVisible: false,
      checkDialogData: [{}],
      checkDialogForm: {
        tagId: "",
        yhbh: "",
        current: 1,
        size: 10,
        total: 0
      },
      labelOptions: [],
      // 录入框
      entryDialogVisible: false,
      entryDialogData: [],
      entryDialogForm: {
        yhbh: "",
        current: 1,
        size: 5,
        total: 0
      },
      uploadParams:{
        current: 1,
        size: 5,
        activityId:'',
        fileUploadId:'',
      },
      actionUrl:CP_PATH+'/cptActivityConfig/uploadExcel',
      tableType:'1',//1===查询，2===文件上传查询
    };
  },
  mounted() {
    this.getTableData()
  },
  methods: {
    //表格样式
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'success-row';
      }
      return '';
    },
    // 分页
    handleSizeChange(val) {
      this.queryForm.size = val
      this.getTableData()
    },
    handleCurrentChange(val) {
      this.queryForm.current = val
      this.getTableData()
    },
    // 点击录入
    clickEntry(row) {
      this.activityId = row.activityId
      window.console.log(row,';;')
      this.getEntryDialogData()
      this.entryDialogVisible = true
    },
    // 点击查看
    clickCheck(row) {
      this.activityId = row.activityId
      this.checkDialogForm.current = 1
      this.checkDialogForm.size = 10
      this.getLabelOptions()
      this.getCheckDialogData()
      this.checkDialogVisible = true
    },
    // 查看框关闭
    checkDialogClose() {
      this.checkDialogVisible = false
    },
    // 查看框分页
    checkDialogSizeChange(val) {
      this.checkDialogForm.size = val
      this.getCheckDialogData()
    },
    checkDialogCurrentChange(val) {
      this.checkDialogForm.current = val
      this.getCheckDialogData()
    },
    //下载
    downFile() {
      window.location.href = CP_PATH + '/cptActivityConfig/downloadTemplate'
    },
    //上传
    beforeUpload(file, fileList) {
      this.uploadParams.activityId = this.activityId
      this.uploadParams.current = this.entryDialogForm.current = 1
      this.uploadParams.size = this.entryDialogForm.size
      var testmsg = file.name.substring(file.name.lastIndexOf('.') + 1)
      let isType = (testmsg === 'xls' || testmsg === 'xlsx')
      if (!isType) {
        this.$alert(`上传文件只能是xls、xlsx格式!`);
      }
      return isType;
    },
    handleSuccess(res,file) {
      this.uploadParams.fileUploadId = res.data.fileUploadId||''
      console.log(file);
      if (res.code === "00000") {
        this.tableType = '2'
        this.$message.success('上传成功')
        res.data.rows.forEach((v)=>{
          v.flag = v.flag==='1'?true:false
        })
        this.entryDialogData = [...res.data.rows]
        this.entryDialogForm.total = res.data.total
        this.entryDialogData.forEach(e => {
          e.bindTagB = e.bindTag === '1' ? true : false
        })
      }
    },
    //文件上传用于分页
    UploadExcel(){
      this.uploadExcel(Qs.stringify({
        current: this.entryDialogForm.current,
        size: this.entryDialogForm.size,
        activityId:  this.uploadParams.activityId,
        fileUploadId: this.uploadParams.fileUploadId,
      })).then(res=>{
        this.tableType = '2'
        res.rows.forEach((v)=>{
          v.flag = v.flag==='1'?true:false
        })
        this.entryDialogData = [...res.rows]
        this.entryDialogForm.total = res.total
        this.entryDialogData.forEach(e => {
          e.bindTagB = e.bindTag === '1' ? true : false
        })
      })
    },
    // 录入框关闭
    entryDialogClose() {
      this.entryDialogVisible = false
    },
    // 录入框分页
    entryDialogSizeChange(val) {
      this.entryDialogForm.size = val
      if (this.tableType==='1'){
        this.getEntryDialogData()
      }else {
        this.UploadExcel()
      }
    },
    entryDialogCurrentChange(val) {
      this.entryDialogForm.current = val
      if (this.tableType==='1'){
        this.getEntryDialogData()
      }else {
        this.UploadExcel()
      }
    },
    // 关闭任务
    clickClose(row) {
      this.$confirm('此操作将关闭改任务, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        window.console.log(row)
        this.closeTask({
          "id":row.id
        }).then(()=>{
          this.getTableData()
          this.$message.info('已关闭')
        })
      }).catch(() => {
      })
    },
    // 获取列表
    getTableData() {
      this.getActivityConfig(this.queryForm).then(res => {
        this.tableData = [...res.records]
        this.queryForm.total = res.total
      })
    },
    // 获取查看列表
    getCheckDialogData() {
      this.getRecommendCustomer({ ...this.checkDialogForm, activityId: this.activityId }).then(res => {
        this.checkDialogData = [...res.records]
        this.checkDialogForm.total = res.total
      })
    },
    // 获取录入列表
    getEntryDialogData() {
      this.customerList({
        size:this.entryDialogForm.size,
        current:this.entryDialogForm.current,
        "activityId":this.activityId,
        "yhbh":this.entryDialogForm.yhbh,
      }).then(res=>{
        this.tableType = '1'
         res.records.forEach((v)=>{
          v.flag = v.flag==='1'?true:false
        })
        this.entryDialogData = res.records
        window.console.log(this.entryDialogData)
        this.entryDialogForm.total = res.total
      })
    },
    // 获取用户标签选项
    getLabelOptions() {
      this.getLabel({
        tagobjId: YDKH_TAG_OBJ,
      }).then(res => {
        if (res.length !== 0) this.labelOptions = res.map(e => {
          return {
            value: e.bqId,
            label: e.bqName,
          }
        })
      })
    },
    //是否参加活动
    activeSelect(row){
      this.$confirm('勾选后将不可逆, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.joinActivity({
          "activityId":this.activityId,
          "yhbh":row.yhbh
        }).then(()=>{
          this.$message.success('勾选参与活动成功')
        })
      }).catch(() => {
        row.flag = false;
        this.$message({
          type: 'info',
          message: '已取消标记'
        });
      });
    }
  }
};
</script>

<style scoped lang="scss">
.executionInformation {
  ::v-deep .el-table .warning-row {
    // background: #e7f6f7;
    background: #e9f3fe;
  }
  ::v-deep .el-table {
    .success-row {
      background: rgb(244, 244, 244);
    }
    .cell {
      padding: 2px 4px;
    }
    th {
      background-color: rgb(24, 153, 250);
      .cell {
        color: #ffffff;
      }
    }
  }
  .content-border {
    background: #ffffff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
  }
  .header {
    height: 40px;
    display: flex;
    align-items: center;
    .select {
      margin-right: 20px;
    }
    .input {
      width: 300px;
      margin-right: 20px;
    }
  }
  .content {
    .content-table {
      width: 100%;
      // height: 350px;
      .text {
        color: #1899fa;
        cursor: pointer;
      }
    }
    .content-page {
      margin-top: 10px;
      width: 100%;
      text-align: center;
    }
  }
  .check-dialog,
  .entry-dialog {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .query-group {
      margin-bottom: 10px;
      .search {
        display: flex;
        align-items: center;
      }
      .select {
        margin-right: 20px;
      }
      .input {
        width: 300px;
        margin-right: 20px;
      }
      .upload {
        margin: 0 10px;
      }
    }

    .dialog-pagination {
      text-align: center;
      margin: 10px 0;
    }
  }
}
</style>
